import {
  navigation,
  toggleButton,
  sliderLine,
  slideWidth,
  nextButton,
  prevButton,
  pagesElement,
  showPopupButton,
  popup,
  closePopupButton,
  modalForm,
  textInput
} from './scripts/utils/constants';

let offset = 0;
let currentPage = 1;

function updatePageIndicator() {
  pagesElement.textContent = currentPage;
}

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
function closePopup () {
  popup.classList.remove('popup_active');
}

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð¿Ð¾ ÐºÐ½Ð¾Ð¿ÐºÐµ esc
function handleEscClose(event) {
  if (event.key === 'Escape') {
    closePopup();
  };
};

// Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð²Ð½Ðµ Ñ„Ð¾Ñ€Ð¼Ñ‹
popup.addEventListener('mousedown', (event) => {
  if (event.target === popup) {
    closePopup();
  };
});

// Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÑŒ Ðº ÐºÐ½Ð¾Ð¿ÐºÐµ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð¹ Ñ„Ð¾Ñ€Ð¼Ñ‹
showPopupButton.addEventListener('click', () => {
  // Ð¾Ñ‡Ð¸Ñ‰Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ðµ Ñ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸ÐµÐ¼ Ð¿ÐµÑ€ÐµÐ´ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸ÐµÐ¼
  document.addEventListener('keydown', handleEscClose);
  textInput.value = '';
  popup.classList.add('popup_active');
});

// ÑÐ»ÐµÐ´Ð¸Ð¼ Ð·Ð° Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸ÐµÐ¼ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
closePopupButton.addEventListener('click', () => {
  document.removeEventListener('keydown', handleEscClose);
  closePopup();
});

// Ð·Ð´ÐµÑÑŒ Ð¼Ð¾Ð¶Ð½Ð¾ Ð¾Ð¿Ð¸ÑÐ°Ñ‚ÑŒ Ð·Ð°Ð¿Ñ€Ð¾Ñ Ðº api Ñ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¾Ð¹ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ñ
modalForm.addEventListener('submit', (e) => {
    e.preventDefault();
    alert(`Ð¡Ð¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ: ${textInput.value}\nÐŸÐ¸ÑÑŒÐ¼Ð¾ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¾ Ð½Ð° rbru-metrika@yandex.ru`);
    popup.classList.remove('popup_active');
});

// Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°ÐµÐ¼ ÑÐ»Ð°Ð¹Ð´
nextButton.addEventListener('click', () => {
  offset = offset + slideWidth;
  if (offset > slideWidth * 3) {
    offset = 0;
    currentPage = 1;
  } else {
    currentPage++;
  }
  sliderLine.style.left = -offset + 'px';
  updatePageIndicator();
});

prevButton.addEventListener('click', () => {
  offset = offset - slideWidth;
  if (offset < 0) {
    offset = slideWidth * 3;
    currentPage = 4;
  } else {
    currentPage--;
  }
  sliderLine.style.left = -offset + 'px';
  updatePageIndicator();
});

toggleButton.addEventListener('click', () => {
  navigation.classList.toggle('navigation_visible');
});

updatePageIndicator();